.offer {
  width: 70vw;
  height: 40vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1vh 1vw;
}
.offer img {
  max-width: 50%;
  max-height: 70%;
  border-radius: 25px;
}
.offer__description {
  padding: 2vh 2vw;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.offer__description .title {
  font-size: 2em;
  color: #db3a34;
}
.offer__description .shortDescription {
  font-size: 0.7em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 20vw;
}
.offer__description .shortDescription span {
  color: #323031;
  text-decoration: underline;
  padding: 10px 0;
}
.offer__description .shortDescription p {
  text-align: left;
}

